<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>
            {{ $t('请选择消费类型') }}
          </v-card-title>
          <v-card-text>
            <v-select
              v-model="activeConsumeTypeIndex"
              :items="consumeTypes"
              item-text="name"
              item-value="id"
              :label="$t('consumeTypeId')"
            />
          </v-card-text>
          <template v-if="activeConsumeTypeIndex">
            <div class="flex justify-space-between">
              <v-btn
                class="primary mb-3 ml-4"
                @click="settingDialog=true"
              >
                {{ $t('Buffet Setting') }}
              </v-btn>

              <v-btn
                color="red mb-3 ml-4"
                @click="resetTime"
              >
                {{ $t('重置全部自助餐时间') }}
              </v-btn>
            </div>
            <v-dialog
              v-model="settingDialog"
              max-width="500"
            >
              <v-card
                max-width="500"
              >
                <v-card-title>
                  {{ $t('自助餐') }}
                  <v-spacer/>
                </v-card-title>
                <v-card-text>
                  <v-row>
                    <v-col>
                      <v-text-field
                        v-model="setting.totalRound"
                        hide-details
                        :label="$t('轮数')"
                      />
                    </v-col>
                    <v-col>
                      <v-text-field
                        v-model="setting.roundTime"
                        hide-details
                        :label="$t('每轮时间')"
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-text-field
                        v-model="setting.maxDineTime"
                        hide-details
                        :label="$t('最大用餐时间')"
                      />
                    </v-col>
                    <v-col>
                      <v-text-field
                        v-model="setting.childRoundDishCount"
                        hide-details
                        :label="$t('儿童每轮数量')"
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-btn
                        color="primary"
                        block
                      >
                        {{ $t('更新') }}
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-dialog>
          </template>
        </v-card>
      </v-col>
    </v-row>
    <template v-if="activeConsumeTypeIndex">
      <v-row>
        <v-col
          cols="12"
          lg="6"
          md="6"
        >
          <v-card>
            <ik-data-table
              :model="buffetPriceModel"
              :filter="filter"
              :entity-name="$t('自助价格菜品')"
              icon="mdi-account-child"
              :use-edit-action="false"
            />
          </v-card>
        </v-col>
        <v-col
          cols="12"
          lg="6"
          md="6"
        >
          <v-card>
            <ik-data-table
              :model="buffetModel"
              :filter="filter"
              :entity-name="$t('时间设置')"
              icon="mdi-account-child"
            />
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          cols="12"
          lg="12"
        >
          <v-card>
            <ik-data-table
              :model="model"
              :filter="filter"
              :entity-name="$t('自助类型设置')"
              icon="mdi-account-child"
              :use-action="false"
            />
          </v-card>
        </v-col>
      </v-row>
    </template>
  </v-container>
</template>

<script>
  import DishesConsumeType from '@/model/DishesConsumeType'
  import { IkDataTable } from 'metaflow-js'
  import ConsumeType from '@/model/ConsumeType'
  import Dish from '@/model/Dish'
  import BuffetTimeSpan from '@/model/BuffetTimeSpan'
  import BuffetPrice from '@/model/BuffetPrice'

  const defaultSetting = {
    roundDishCount: 0,
    childRoundDishCount: 0,
    maxDineTime: 0,
    roundTime: 0,
    totalRound: 0,
  }
  export default {
    name: 'BuffetSetting',
    components: {
      IkDataTable,
    },
    data: function () {
      return {
        model: DishesConsumeType,
        buffetModel: BuffetTimeSpan,
        buffetPriceModel: BuffetPrice,
        consumeTypes: [],
        settingDialog: false,
        dishes: [],
        activeConsumeTypeIndex: null,
        setting: defaultSetting,
      }
    },
    computed: {
      filter: function () {
        return this.activeConsumeTypeIndex ? { consumeTypeId: this.activeConsumeTypeIndex } : null
      },
    },
    async mounted () {
      this.consumeTypes = await ConsumeType.getList()
      this.dishes = await Dish.getList()
    },
    methods: {
      async resetTime () {
        const temp = await BuffetTimeSpan.getList()
        console.log('async timespan:', temp, 'typeof fromTime', typeof (temp[0].fromTime))
        await BuffetTimeSpan.resetTimeSpan(temp)
      },
    },
  }
</script>

